import React from "react";
import { ArrowLeft, X } from "react-feather";
import { ActionTypes, formatCamelCase } from "../../Classes/Constants";
import SettingsStore, { defaultSettingsSchematic as schema, settingsDispatcher } from "../../Classes/Types/Stores/SettingsStore";
import { Modals } from "../Modals";
import "./SettingsModal.scss";

export default function SettingsModal() {
	const [selected, _select] = React.useState(null);
	const [opacity, setOpacity] = React.useState(1);
	const [query, search] = React.useState("");

	let transitioning = false;
	async function select(category) {
		if (transitioning) return;
		transitioning = true;

		setOpacity(0);

		await new Promise(r => setTimeout(r, 150));

		_select(category);
		setOpacity(1);
	}

	const filters = {
		root: category => {
			if (!query) return true;
			if (category.toLowerCase().includes(query)) return true;

			for (const subCategory in schema[category]) {
				if (filters.sub(subCategory)) {
					return true;
				}

				if (filters.items(category, subCategory)) return true;
			}

			return false;
		},
		sub: subCategory => {
			if (!query) return true;
			if (subCategory.toLowerCase().includes(query)) return true;
		},
		items: (root, sub) => {
			if (!query) return true;

			for (const item in schema[root][sub]) {
				if (item.toLowerCase().includes(query)) {
					return true;
				}
			}

			return false;
		}
	};

	return (
		<div className="SettingsModal FullScreenModal FlexCenter">
			<div className="Inner">
				<input
					type="text"
					className="SearchField"
					placeholder="Search..."
					onInput={e => search(e.target.value.toLowerCase())}
				/>

				<div style={{ opacity }} className="Contents">
					{!selected && (
						<div className="Categories FlexCenter">
							{Object.keys(schema).filter(filters.root).map(category => !category.startsWith("__") && (
								<div
									className="CategoryBubble"
									key={category}
									onClick={() => select(category)}
								>
									<div className="FlexCenter">
										{schema[category].__icon}

										<h4 className="Title">
											{formatCamelCase(category)}
										</h4>
									</div>

									<div className="Sections">
										{Object.keys(schema[category]).map(subCategory => (
											formatCamelCase(subCategory).toLowerCase()
										)).filter(s => !s.startsWith("__")).join(", ")}
									</div>
								</div>
							))}
						</div>
					)}

					{selected && (
						<React.Fragment>
							<div className="Categories FlexCenter BackButton">
								<div
									className="CategoryBubble"
									onClick={() => select(null)}
								>
									<div className="FlexCenter">
										<ArrowLeft />

										<h4 className="Title">Back To Menus</h4>
									</div>
								</div>
							</div>

							<div className="Sections">
								{Object.keys(schema[selected]).map(sub => (
									!!Object.keys(schema[selected][sub]).length && !sub.startsWith("__") && (
										<Section
											key={sub}
											cat={selected}
											sub={sub}
										/>
									)
								))}
							</div>

						</React.Fragment>
					)}
				</div>
			</div>

			<div className="Buttons">
				<X
					className="Button"
					onClick={() => Modals.pop()}
				/>
			</div>
		</div>
	);
}

function Section({ cat, sub }) {
	return (
		<div className="Section">
			<h3 className="Title">{formatCamelCase(sub)}</h3>

			<div className="Fields">
				{Object.entries(schema[cat][sub]).map(e => (
					<Field
						key={e[0]}
						path={[cat, sub, e[0]]}
						setting={e[1]}
					/>
				))}
			</div>
		</div>
	);
}

function Field({ path, setting }) {
	const value = SettingsStore.getSettingValue(path);

	const field = React.useMemo(() => {
		switch (setting.type) {
			default: return null;

			case "string": {
				return (
					<input
						className="InputField StringField"
						defaultValue={value}
						onBlur={e => settingsDispatcher.dispatch({
							type: ActionTypes.UPDATE_SETTING,
							value: e.target.value,
							path
						})}
						{...(setting.props ?? {})}
					/>
				);
			}
		}
	}, []);

	return (
		<div className="SettingField">
			<h4 className="Title">
				{formatCamelCase(path[path.length - 1])}
			</h4>

			{setting.note && (
				<div className="Note">
					{setting.note}
				</div>
			)}

			<div className="Field">
				{field}
			</div>
		</div>
	);
}