import _ from "lodash";
import "./Styles/Groups.scss";
import { ChevronDown, MessageSquare, Mic, Phone, Rss } from "react-feather";
import { ActionTypes, GroupType, joinClassNames } from "../Constants";
import { dispatcher } from "../Dispatcher";
import { pushHistoryState } from "../../App";
import { addDoc, collection, serverTimestamp } from "@firebase/firestore";
import UserStore from "./Stores/UserStore";
import GroupStore from "./Stores/GroupStore";
import Database from "../Database";
import CircleStore from "./Stores/CircleStore";
import User from "./User";
import VoiceModule, { ChatRoom } from "../VoiceModule";
import React from "react";
import { useInterval } from "../Hooks";
import MessageCountStore from "./Stores/MessageCountStore";
import VoiceMemberStore from "./Stores/VoiceMemberStore";

export default class Group {
	id = "";
	title = "";

	constructor(group) {
		_.extend(this, group);
	}

	get isNsfw() {
		return this.nsfw;
	}

	get circle() {
		return CircleStore.getCircle(this.circleId);
	}

	sendMessage(content) {
		// I fucking hate Firebase with a passion.
		setTimeout(() => this.markAsRead(), 2000);

		return addDoc(collection(this._ref, "messages"), {
			senderId: UserStore.getLocalUser()?.id,
			content,
			creationDate: serverTimestamp()
			// TODO add mentions
		});
	}

	markAsRead() {
		if (this.isDm) {
			MessageCountStore.markRead(this.groupId);
		}
		else {
			MessageCountStore.markRead(this.circleId, this.id);
		}
	}

	Component = props =>
		<GroupComponent group={this} {...props} />;
}

function GroupComponent({ group, children = null }) {
	dispatcher.useForceUpdater(
		ActionTypes.GROUP_UPDATE,
		({ circle, group }) => circle.id === group.circleId
			&& group.id === group.id
	);

	if (group.isNsfw && !User.isNsfwVerified)
		return null;

	switch (group.type) {
		default: return null;

		case GroupType.FOLDER: {
			return (
				<GroupFolder group={group}>
					{children}
				</GroupFolder>
			);
		}

		case GroupType.TEXT: {
			return <GroupText group={group} />;
		}

		case GroupType.VOICE: {
			return <GroupVoice group={group} />;
		}

		case GroupType.ANNOUNCEMENTS: {
			return <GroupAnnouncements group={group} />;
		}
	}
}

export function GroupFolder({ group, children }) {
	return (
		<div className="Group TypeFolder">
			<div className="Title">
				<ChevronDown /> {group.title}
			</div>

			<div className="FolderContents">
				{children}
			</div>
		</div>
	);
}

function selectGroup(group) {
	pushHistoryState(
		group.circle.lastLocation =
		`app/circles/${group.circleId}/groups/${group.id}`
	);

	MessageCountStore.markRead(group.circle.id, group.id);
}

function GroupText({ group }) {
	const events = {
		onClick: () => selectGroup(group)
	};

	return (
		<div className={
			joinClassNames(
				"Group",
				"TypeText",
				[GroupStore.getSelectedGroup() === group, "Selected"]
			)
		}>
			<div className="Title" {...events}>
				<MessageSquare /> {group.title}

				<GroupUnreadBlips group={group} />
			</div>
		</div>
	);
}

function GroupUnreadBlips({ group }) {
	const unreadCount = MessageCountStore.useState(() =>
		MessageCountStore.getUnreadMessageCount(group.circleId, group.id));

	return (
		<div className="UnreadBlips">
			{!!unreadCount && (
				<div className="Blip Unread">
					{unreadCount > 999
						? `${Math.round(unreadCount / 1000)}k`
						: unreadCount}
				</div>
			)}
		</div>
	);
}

function GroupVoice({ group }) {
	const members = VoiceMemberStore.useState(() =>
		VoiceMemberStore.getVoiceMembers(group.circleId, group.id));

	function selectText() {
		selectGroup(group);
	}

	function connectToVoice() {
		if (VoiceModule.currentRoom?.group.id === group.id) {
			pushHistoryState(`#app/circles/${group.circleId}/call`);
		}
		else {
			new ChatRoom(group.circleId, group.id).connect();
		}
	}

	return (
		<div className="Group TypeVoice">
			<div className="Title">
				<Mic /> {group.title}
			</div>

			<div className="FolderContents">
				<div
					className={
						joinClassNames(
							"Group",
							"TypeText",
							[GroupStore.getSelectedGroup() === group, "Selected"]
						)
					}
					onClick={selectText}
				>
					<div className="Title">
						<MessageSquare /> Text Chat

						<GroupUnreadBlips group={group} />
					</div>
				</div>

				<div
					className={
						joinClassNames(
							"Group",
							"TypeText",
							[VoiceModule.currentRoom?.group === group, "Selected"]
						)
					}
					onClick={connectToVoice}
				>
					<div className="Title">
						<Phone /> {VoiceModule.currentRoom?.group === group
							? "Connected" : "Join Room"}
					</div>

					<div className="FolderContents">
						{members && members.map(userId => (
							<VoiceRoomMember key={userId} userId={userId} />
						))}
					</div>
				</div>
			</div>
		</div>
	);
}

function VoiceRoomMember({ userId }) {
	const room = VoiceModule.currentRoom;
	const user = UserStore.useState(() => UserStore.getUser(userId));

	console.log({ room });

	return (
		<div className="Group TypeText TypeUser">
			<div className="Title">
				<user.AvatarComponent size={17} />

				{user.displayName}
			</div>
		</div>
	);
}

function GroupAnnouncements({ group }) {
	return (
		<div className="Group TypeAnnouncement">
			<div className="Title">
				<Rss /> {group.title}
			</div>
		</div>
	);
}

export class GroupDm extends Group {
	userId = "";
	lastMessageDate;

	get isNsfw() {
		return false;
	}

	get _ref() {
		return Database.doc(
			"directmessages",
			[UserStore.getLocalUser().id, this.userId].sort().join("_")
		);
	}

	Component = () =>
		<DmGroupComponent group={this} />;
}

export function DmGroupComponent({ group }) {
	const user = UserStore.useState(() => UserStore.getUser(group.userId));

	return user?.displayName ? (
		<div
			className="DirectMessageGroup UserSection Flex"
			onClick={() => pushHistoryState(`#app/dms/${user.id}`)}
		>
			<user.AvatarComponent />

			<div className="Details">
				<h3 className="Username">{user.displayName}</h3>

				<user.StatusText tooltipDirection="none" />
			</div>
		</div>
	) : null;
}