import _ from "lodash";
import React from "react";
import { ActionTypes, joinClassNames } from "../Constants";
import { dispatcher } from "../Dispatcher";
import EmoteStore from "./Stores/EmoteStore";
import "./Styles/Emote.scss";

export default class Emote {
	id;
	url;
	packId;

	constructor(emote) {
		_.extend(this, emote);
	}

	Component = () =>
		<EmoteComponent emote={this} />;
}

export function EmoteComponent({ emote }) {
	const ref = React.useRef();
	const [size, setSize] = React.useState("1.25em");

	const url = EmoteStore.useState(() => emote.url);

	React.useEffect(() => {
		if (ref.current?.parentElement?.childNodes.length === 1) {
			setSize("4em");
		}
	}, []);

	return (
		<div
			className="Emote"
			ref={ref}
			style={{
				width: size,
				height: size
			}}
		>
			<img
				className="EmoteImage"

				src={url}
				alt=""
			/>
		</div>
	);
}