import _ from "lodash";
import { ActionTypes, playSound } from "../../Constants";
import { dispatcher } from "../../Dispatcher";
import Store from "../../Store";

import Ascend from "../../../Assets/Audio/ascend.ogg";
import Descend from "../../../Assets/Audio/descend.ogg";
import VoiceModule from "../../VoiceModule";

const cache = {};

const VoiceMemberStoreClass = class VoiceMemberStore extends Store {
	getVoiceMembers(circleId, groupId) {
		return _.get(cache, [circleId, groupId]);
	}

	getAllVoiceMembers() {
		return cache;
	}
}

const VoiceMemberStore = new VoiceMemberStoreClass(dispatcher, {
	[ActionTypes.UPDATE_VOICE_MEMBERS]: ({ circleId, groupId, users }) => {
		const last = _.get(cache, [circleId, groupId], []);

		if (VoiceModule.currentRoom?.group.id === groupId) {
			// Handle user disconnections
			for (const userId of last) {
				if (!~users.indexOf(userId)) {
					playSound(Descend, 0.5);
				}
			}

			// Handle user connections
			for (const userId of users) {
				if (!~last.indexOf(userId)) {
					playSound(Ascend, 0.5);
				}
			}
		}

		_.set(cache, [circleId, groupId], users);
	}
});

export default VoiceMemberStore;