import React from "react";
import _ from "lodash";
import "./Styles/User.scss";
import { ActionTypes, formatDateString, joinClassNames, mib, openFileDialog, UserStatus } from "../Constants";
import UserStore from "./Stores/UserStore";
import Tooltip from "../../Components/Tooltip";
import { Clipboard, Image, Maximize, MessageSquare, User as UserFeather } from "react-feather";
import ContextMenu from "../../Components/ContextMenuHandler";
import { copyToClipboard, Modals, openImageModal } from "../../Components/Modals";
import UserProfileModal from "../../Components/Modals/UserProfileModal";
import Toasts from "../../Components/Toasts";
import Database from "../Database";
import { dispatcher } from "../Dispatcher";
import { pushHistoryState } from "../../App";

export default class User {
	id = "";
	displayName = "";
	avatarUrl = "";

	constructor(user) {
		_.extend(this, user);
	}

	AvatarComponent = props =>
		<UserAvatarComponent user={this} {...props} />;

	StatusText = props =>
		<UserStatusText user={this} {...props} />;

	Mention = () =>
		<UserMention user={this} />;

	ContextMenu = () =>
		<UserContextMenu user={this} />;

	// TODO add modal
	static async selectAvatar() {
		const [file] = await openFileDialog({ type: "image/*" });
		if (!file) return;

		if (file.size > mib(8))
			return Toasts.showToast(
				"You cannot upload an avatar above 8MiB",
				"Failure"
			);

		Database.uploadStorage(file, "avatar", UserStore.getLocalUser().id)
			.then(r => (this.avatarUrl = r.url, Toasts.showToast("Avatar updated")))
			.catch(err => (console.error(err), Toasts.showToast("There was an error uploading your avatar", "Failure")))
	}

	static get isNsfwVerified() {
		return UserStore.getLocalUser()?.nsfwVerified;
	}
}

export function UserAvatarComponent({ user, size = 40, className = "" }) {
	const avatarUrl = UserStore.useState(() => user.avatarUrl);
	const displayName = UserStore.useState(() => user.displayName);

	const [failed, setFailed] = React.useState(false);

	return (
		<div
			className={joinClassNames("AvatarContainer StyleSquare FlexCenter", className)}
			style={{ width: size, height: size }}
		>
			{failed
				? (
					<UserFeather
						className="DefaultAvatar"
						style={{ width: size / 1.5, height: size / 1.5 }}
					/>
				)
				: (
					<img
						className="Avatar"
						src={avatarUrl}
						alt={displayName}
						width={size} height={size}

						onError={() => setFailed(true)}
					/>
				)}
		</div>
	);
}

export function UserStatusText({ user, tooltipDirection = "right" }) {
	const status = UserStore.useState(() => user.status);
	const lastOnline = user.lastOnline ? new Date(user.lastOnline) : null;

	switch (status) {
		default:
		case UserStatus.OFFLINE: {
			return (
				<div className="Flex StatusContainer">
					<div className="Status Offline" />
					<div>Offline</div>

					{lastOnline && tooltipDirection !== "none" && (
						<Tooltip direction={tooltipDirection}>
							Last online {formatDateString(lastOnline)} at {lastOnline.toLocaleTimeString("en-US")}
						</Tooltip>
					)}
				</div>
			);
		}

		case UserStatus.ONLINE: {
			return (
				<div className="Flex StatusContainer">
					<div className="Status Online" />
					<div>Online</div>
				</div>
			);
		}

		case UserStatus.AWAY: {
			return (
				<div className="Flex StatusContainer">
					<div className="Status Away" />
					<div>Away</div>
				</div>
			);
		}

		case UserStatus.BUSY: {
			return (
				<div className="Flex StatusContainer">
					<div className="Status Busy" />
					<div>Busy</div>
				</div>
			);
		}

		case UserStatus.BOT: {
			return (
				<div className="Flex StatusContainer">
					<div className="Status Bot" />
					<div>Bot</div>
				</div>
			);
		}
	}
}

export function UserMention({ user }) {
	dispatcher.useForceUpdater(
		ActionTypes.USER_UPDATE,
		e => e.user.id === user.id
	);

	// Do this garbage because of inline mentions.
	// Don't remove this, you'll get angry later.
	user = UserStore.getUser(user.id);

	return (
		<ContextMenu.Wrapper
			menu={<user.ContextMenu />}
			wrapperClassName="UserMention"
		>
			<UserAvatarComponent user={user} size={19} />
			<div className="Username">{user.displayName}</div>
		</ContextMenu.Wrapper>
	);
}

function UserContextMenu({ user }) {
	return (
		<ContextMenu>
			<ContextMenu.Item
				icon={<UserFeather />}
				onClick={() => Modals.push(<UserProfileModal userId={user.id} />)}
				autoClose
			>Profile</ContextMenu.Item>

			<ContextMenu.Item
				icon={<MessageSquare />}
				onClick={() => pushHistoryState(`app/dms/${user.id}`)}
				autoClose
			>Message</ContextMenu.Item>

			{user.avatarUrl && (
				<ContextMenu.SubMenuItem
					label="Avatar"
					icon={<Image />}
				>
					<ContextMenu.Item
						icon={<Maximize />}
						onClick={() => openImageModal(user.avatarUrl)}
						autoClose
					>View</ContextMenu.Item>

					<ContextMenu.Item
						icon={<Clipboard />}
						onClick={() => copyToClipboard(user.avatarUrl)}
						autoClose
					>Copy URL</ContextMenu.Item>
				</ContextMenu.SubMenuItem>
			)}
		</ContextMenu>
	);
}