import React from "react";
import { appHashArgs } from "../../App";
import { ActionTypes } from "../../Classes/Constants";
import { dispatcher } from "../../Classes/Dispatcher";
import CircleStore from "../../Classes/Types/Stores/CircleStore";
import CirclePage from "../AppPages/CirclePage";
import DmsPage from "../AppPages/DmsPage";
import "./CorePanel.scss";

export default function CorePanel() {
	dispatcher.useForceUpdater(ActionTypes.PAGE_UPDATE);

	switch (appHashArgs[0]) {
		case undefined:
		case "": {
			return (
				<div className="AppHome FlexCenter">
					<h2>
						Welcome to the new Cirlos!
					</h2>

					<p>
						Cirlos has been completely redesigned and rewritten, from the ground up.
					</p>

					<p>
						This means better UI/UX, stability, and performance.
					</p>
				</div>
			);
		}

		case "circles": {
			const circle = CircleStore.getCircle(appHashArgs[1]);

			if (circle) {
				return (
					<CirclePage circle={circle} />
				);
			}
			else {
				return (
					<div className="AppHome FlexCenter">
						<h2>
							Oops!
						</h2>

						<p>
							This circle could not be loaded properly.
						</p>
					</div>
				);
			}
		}

		case "dms":
			return <DmsPage />;
	}

	// TODO add 404
	return (
		<div className="AppHome FlexCenter">
			This page does not exist.
		</div>
	);
}