const settings = {};

const SettingsStoreClass = class SettingsStore extends Store {
	constructor(...args) {
		super(...args);

		const localUserListener = () => {
			const localUser = UserStore.getLocalUser();

			if (localUser) {
				this._databaseReference = Database.doc("usersettings", UserStore.getLocalUser().id, "v2", "properties");

				Database.getDoc(
					this._databaseReference,
				).then(data => {
					this._hasLoaded = true;

					settingsDispatcher.dispatch({
						type: ActionTypes.BATCH_UPDATE_SETTINGS,
						entries: data ?? {}
					});
				});

				UserStore.removeListener(localUserListener);
			}
		};

		UserStore.addListener(localUserListener);

		this._save = _.debounce(this._save.bind(this), 5000);
	}

	_save() {
		if (!this._hasLoaded) return;

		Database.set(this._databaseReference, settings)
			.catch(err => console.error("Error saving settings!", err.stack));
	}

	getSettingValue(...path) {
		// In case we pass an array instead of a de-structured array
		if (Array.isArray(path[0])) path = path[0];

		const def = _.get(defaultSettingsSchematic, path);

		if (typeof (def) === "undefined") {
			return console.error("No setting at path", path.join("."));
		}

		return _.get(settings, [...path, "value"], def.value);
	}
}

export const settingsDispatcher = new Dispatcher;

const SettingsStore = new SettingsStoreClass(settingsDispatcher, {
	[ActionTypes.UPDATE_SETTING]: ({ path, value }) => {
		if (_.get(defaultSettingsSchematic, path)) {
			_.set(settings, [...path, "value"], value);

			SettingsStore._save();
		}
		else {
			console.warn("Invalid setting was passed!", { path, value });
		}
	},
	[ActionTypes.BATCH_UPDATE_SETTINGS]: ({ entries }) => {
		_.extend(settings, entries);
	}
});

export default SettingsStore;

export function SettingsCssRenderer() {
	settingsDispatcher.useForceUpdater(ActionTypes.UPDATE_SETTING);
	settingsDispatcher.useForceUpdater(ActionTypes.BATCH_UPDATE_SETTINGS);

	const renderers = {
		"appearance.background.image": value => `
			.Core::before,
			.SettingsModal::before {
				content: "";

				background-image: url("${value}");
				background-position: center;
				background-size: cover;

				position: fixed;
				width: 100vw;
				height: 100vh;
			}

			.SettingsModal .Section {
				backdrop-filter: blur(20px);
			}
		`
	};

	return (
		<style>
			{Object.keys(renderers).map(path => (
				renderers[path](SettingsStore.getSettingValue(...path.split(".")))
			)).join("\n")}
		</style>
	);
}