import React from "react";
import { PhoneOff } from "react-feather";
import { ActionTypes } from "../../Classes/Constants";
import { dispatcher } from "../../Classes/Dispatcher";
import UserStore from "../../Classes/Types/Stores/UserStore";
import VoiceMemberStore from "../../Classes/Types/Stores/VoiceMemberStore";
import VoiceModule from "../../Classes/VoiceModule";
import "./CallPanel.scss";

export default function CallPanel() {
	dispatcher.useForceUpdater(ActionTypes.UPDATE_CURRENT_CALL);

	const call = VoiceModule.currentRoom;

	dispatcher.useForceUpdater(
		ActionTypes.UPDATE_VOICE_MEMBERS,
		({ groupId }) => groupId === call?.group.id
	);

	const memberIds = (call?.group
		? VoiceMemberStore.getVoiceMembers(call.group.circleId, call.group.id)
		: []) ?? [];

	return call
		? (
			<div className="Panel PanelCall">
				<div className="Members FlexCenter">
					{memberIds.map(userId => (
						<VoiceCallMember key={userId} userId={userId} />
					))}
				</div>
			</div>
		)
		: (
			<div className="Panel PanelCall NoCall">
				<PhoneOff /> Call ended.
			</div>
		);
}

function VoiceCallMember({ userId }) {
	const user = UserStore.useState(() => UserStore.getUser(userId));

	return user ? (
		<div className="VoiceMember">
			<user.AvatarComponent size={200} />

			<h4 className="Username">{user.displayName}</h4>
		</div>
	) : null;
}