import "./Styles/Circles.scss";
import _ from "lodash";
import Tooltip from "../../Components/Tooltip";
import { ActionTypes, getAcronym, joinClassNames, UserStatus } from "../Constants";
import { dispatcher } from "../Dispatcher";
import { appHashArgs, pushHistoryState } from "../../App";
import Database from "../Database";
import { collection, serverTimestamp } from "@firebase/firestore";
import Group from "./Group";
import GroupStore from "./Stores/GroupStore";
import { Compass, LogOut } from "react-feather";
import MessageCountStore from "./Stores/MessageCountStore";
import UserStore from "./Stores/UserStore";
import Toasts from "../../Components/Toasts";
import { openBoolModal } from "../../Components/Modals";
import ContextMenu from "../../Components/ContextMenuHandler";

let fetchingGroups = false;
export default class Circle {
	id = "";
	name = "";
	iconUrl = "";

	constructor(circle) {
		_.extend(this, circle);
	}

	_groups = null;

	getGroups() {
		return GroupStore.getGroups(this.id);
	}

	async leaveCircle() {
		const confirmation = await openBoolModal({
			title: "Are you sure?",
			description: `Leave <b>${this.name}</b>?`
		});

		if (!confirmation) return;

		// Delete member item
		await Database.delete(
			Database.doc("circles", this.id, "members", UserStore.getLocalUser().id)
		);

		// Delete circle from user data
		await Database.delete(
			Database.doc("userdata", UserStore.getLocalUser().id, "circles", this.id)
		);

		if (window.location.href.includes(this.id)) {
			pushHistoryState("app");
		}

		Toasts.showToast(<span>Left <b>${this.name}</b></span>);
	}

	renderGroups() {
		const _groups = this.getGroups();

		if (_groups) {
			const groups = Object.values(_groups).sort((a, b) => a.position - b.position);

			const populate = group => (
				<group.Component key={group.id}>
					{groups.filter(g => g.parent === group.id).map(populate)}
				</group.Component>
			);

			return [
				<div
					className={joinClassNames(
						"Group TypeButton",
						[appHashArgs[2] === "overview", "Selected"]
					)}
					key="__overview"
				>
					<div
						className="Title"
						onClick={() => pushHistoryState(`#app/circles/${appHashArgs[1]}/overview`)}
					>
						<Compass /> Overview
					</div>
				</div>,

				...groups.filter(g => !g.parent || !_groups[g.parent]).map(populate)
			];
		}
	}

	BubbleComponent = () =>
		<CircleBubble circle={this} />;

	ContextMenu = () =>
		<CircleContextMenu circle={this} />;

	static async join(circleId, inviteId, userId = UserStore.getLocalUser().id) {
		// TODO add circle rules modal

		// Get member doc
		const doc = Database.doc("circles", circleId, "members", userId);

		// Check if the user is already in the circle
		if (await Database.getDoc(doc)) return;

		// Attempt to join
		const { error } = await Database.set(doc, {
			joinDate: serverTimestamp(),
			inviteId
		}).then(() => ({ error: null })).catch(error => ({ error }));

		// Handle any errors
		if (error) {
			Toasts.showToast("There was an error joining circle", "Failure");

			return console.error("Error joining circle", error);
		}

		// Add the circle to the user data
		await Database.set(
			Database.doc("userdata", userId, "circles", circleId),
			{ position: 0 }
		);
	}
}

export function CircleContextMenu({ circle }) {
	return (
		<ContextMenu>
			<ContextMenu.Item
				icon={<LogOut />}
				style={{ color: "var(--busy-color)" }}
				autoClose
				onClick={circle.leaveCircle.bind(circle)}
			>Leave</ContextMenu.Item>
		</ContextMenu>
	);
}

export function CircleBubble({ circle, icon = null, onClick = null }) {
	dispatcher.useForceUpdater(
		ActionTypes.CIRCLE_UPDATE,
		e => e.circle.id === circle.id
	);

	const unreadCount = MessageCountStore.useState(() =>
		MessageCountStore.getUnreadMessageCount(circle.id));

	const events = {
		onClick: () => {
			if (onClick) return onClick();

			const group = localStorage.getItem(`lastGroup.${circle.id}`) || "overview";

			if (circle.lastLocation) {
				pushHistoryState(circle.lastLocation);
			}
			else {
				pushHistoryState(`app/circles/${circle.id}/${group}`);
			}
		}
	};

	return (
		<ContextMenu.Wrapper menu={circle instanceof Circle ? <circle.ContextMenu /> : null}>
			<div className="Circle" {...events}>
				{icon
					? icon
					: circle.iconUrl
						? <img
							className="Icon"
							src={circle.iconUrl}
							alt={circle.name}
						/>
						: <h3 className="Icon Acronym">
							{getAcronym(circle.name)}
						</h3>}

				<div className="UnreadBlips Flex">
					{unreadCount > 0 && (
						<div className="Blip Unread">
							{unreadCount > 999
								? `${Math.round(unreadCount / 1000)}k`
								: unreadCount}
						</div>
					)}
				</div>

				<Tooltip direction="right">{circle.name}</Tooltip>
			</div>
		</ContextMenu.Wrapper>
	);
}