import _ from "lodash";
import { Modals } from "../Components/Modals";
import { ActionTypes } from "./Constants";
import { dispatcher } from "./Dispatcher";
import CircleStore from "./Types/Stores/CircleStore";
import GroupStore from "./Types/Stores/GroupStore";
import UserStore from "./Types/Stores/UserStore";

export const VoiceEvents = {
	UPDATE_MEMBERS: "UPDATE_MEMBERS",
	INITIALIZE: "INITIALIZE",
	CONNECT: "CONNECT",
	DISCONNECT: "DISCONNECT",
	ADD_TRACK: "ADD_TRACK",
	PC_ANSWER: "PC_ANSWER",
	SEND_ICE: "SEND_ICE",
	REQUEST_CONSUMER_CONNECTION: "REQUEST_CONSUMER",
	RECEIVE_CONSUMER: "RECEIVE_CONSUMER",
	UPDATE_CONSUMER_ICE: "UPDATE_CONSUMER_ICE"
};

const VoiceModule = new class VoiceModule {
	currentRoom = null;

	async initialize() {
		if (this.initialized) return;
		this.initialized = true;

		const address = "192.99.69.216";
		const debugAddress = window.location.hostname;
		const port = "46236";

		const newSocket = () => new WebSocket(`ws://${address}:${port}`);
		this.socket = newSocket();

		this.socket.onerror = err => console.error("your mom", err);

		console.log("socket", this.socket);

		this.socket.addEventListener("message", ({ data }) => {
			const event = JSON.parse(data);

			console.log(
				"voice ws message",
				event
			);

			switch (event.type) {
				case VoiceEvents.UPDATE_MEMBERS: {
					dispatcher.dispatch({
						...event,
						type: ActionTypes.UPDATE_VOICE_MEMBERS
					});
				} break;
			}
		});

		// this.socket.addEventListener("close", () => {
		// 	this.socket = newSocket();
		// });

		dispatcher.subscribe(ActionTypes.CIRCLES_UPDATE, async ({ circles }) => {
			const circleIds = circles.map(c => c.id);

			while (this.socket.readyState !== WebSocket.OPEN)
				await new Promise(r => setImmediate(r));

			this.socket.send(
				JSON.stringify({
					type: VoiceEvents.INITIALIZE,
					userId: UserStore.getLocalUser().id,
					circleIds
				})
			);
		});
	}
}

const iceOptions = {
	iceServers: [{
		urls: ["stun:192.99.69.216:46235"]
	}],
	iceCandidatePoolSize: 10
};

export class ChatRoom {
	connections = {};

	constructor(circleId, groupId) {
		const group = GroupStore.getGroup(circleId, groupId);

		if (!group) {
			console.error("Unable to find voice group!", { circleId, groupId });

			return null;
		}

		this.group = group;

		// Bind events
		this.handleMessage = this.handleMessage.bind(this);
	}

	emitSocket(event) {
		console.log("emitting socket event", event);
		VoiceModule.socket.send(JSON.stringify(event));
	}

	async connect() {
		if (VoiceModule.currentRoom)
			await VoiceModule.currentRoom.disconnect();

		console.log("should connect");

		VoiceModule.socket.addEventListener("message", this.handleMessage);

		this.initializePeerConnection().catch(
			console.error.bind("Failed to initialize peer connection!")
		);

		dispatcher.dispatch({
			type: ActionTypes.UPDATE_CURRENT_CALL,
			call: this
		});

		VoiceModule.currentRoom = this;
	}

	createPeerConnection() {
		return _.extend(
			new RTCPeerConnection(iceOptions),
			{
				onicecandidate: this.handleIceCandidate.bind(this),
				onnegotiationneeded: this.handleNegotiation.bind(this)
			}
		);
	}

	async handleIceCandidate({ candidate }) {
		if (candidate?.candidate?.length) {
			this.emitSocket({
				type: VoiceEvents.SEND_ICE,
				ice: candidate
			});
		}
	}

	async handleNegotiation(peer, type) {
		const offer = await this.pc.createOffer();
		await this.pc.setLocalDescription(offer);

		this.emitSocket({
			type: VoiceEvents.CONNECT,
			sdp: this.pc.localDescription,
			circleId: this.group.circleId,
			groupId: this.group.id
		});
	}

	async initializePeerConnection() {
		const stream = await navigator.mediaDevices.getUserMedia({
			video: false,
			audio: true
		});

		this.handleTrack(stream, UserStore.getLocalUser().id);
		this.localStream = stream;

		this.pc = this.createPeerConnection();

		for (const track of stream.getTracks()) {
			this.pc.addTrack(track, stream);
		}
	}

	handleTrack(stream, userId) {
		// TODO handle video, and don't do this
		if (userId === UserStore.getLocalUser().id) return;

		const id = `vc-aud-${userId}`;

		const audio = _.assign(
			document.getElementById(id) ?? document.createElement("audio"),
			{
				id,
				style: "display:none",
				volume: 0.5,
				srcObject: stream,
				autoplay: true
			}
		);

		console.log({ audio, stream, userId });

		document.body.appendChild(audio);
	}

	async disconnect() {
		console.log("should disconnect");

		VoiceModule.socket.removeEventListener("message", this.handleMessage);

		this.emitSocket({ type: VoiceEvents.DISCONNECT });

		dispatcher.dispatch({
			type: ActionTypes.UPDATE_CURRENT_CALL,
			call: null
		});

		VoiceModule.currentRoom = null;
	}

	async requestConsumer(userId) {
		const pc = new RTCPeerConnection(iceOptions);

		_.extend(this.connections, {
			[userId]: {
				pc
			}
		});

		const transceiverOptions = {
			direction: "recvonly"
		};

		pc.addTransceiver("video", transceiverOptions);
		pc.addTransceiver("audio", transceiverOptions);

		const offer = await pc.createOffer();
		await pc.setLocalDescription(offer);

		pc.onicecandidate = ({ candidate }) => {
			if (candidate?.candidate?.length) {
				this.emitSocket({
					type: VoiceEvents.UPDATE_CONSUMER_ICE,
					ice: candidate,
					userId
				});
			}
		};

		pc.ontrack = e => this.handleTrack(e.streams[0], userId);

		this.emitSocket({
			type: VoiceEvents.REQUEST_CONSUMER_CONNECTION,
			userId,
			sdp: pc.localDescription
		});
	}

	handleMessage({ data }) {
		const event = JSON.parse(data);

		switch (event.type) {
			case VoiceEvents.PC_ANSWER: {
				const sdp = new RTCSessionDescription(event.sdp);
				this.pc.setRemoteDescription(sdp).catch(console.error);
			} break;

			case VoiceEvents.UPDATE_MEMBERS: {
				for (const userId of event.users) {
					if (userId !== UserStore.getLocalUser().id) {
						if (!this.connections[userId]) {
							this.requestConsumer(userId).catch(
								console.error.bind(
									null,
									"Failed to request consumer!"
								)
							);
						}
					}
				}
			} break;

			case VoiceEvents.RECEIVE_CONSUMER: {
				const sdp = new RTCSessionDescription(event.sdp);
				const { pc } = _.get(this.connections, [event.userId], {});

				pc?.setRemoteDescription(sdp)
					.catch(console.error.bind(null, "Failed to receive consumer!"));
			} break;
		}
	}
}

export default VoiceModule;